.canvas{position:relative}.layout-control-panel{display:flex;width:100%;background:#fff;padding:16px;border-right:1px solid #ccc}.layout-control-panel hr,.layout-control-panel .button-view-map,.layout-control-panel .button-view-feeder{display:none}.layout-control-panel .select-view{margin-right:16px}.layout-control-panel .button-view-filters{margin-right:8px}@media only screen and (min-width: 768px){.layout-control-panel{display:flex;width:280px;position:absolute;left:16px;top:16px}.layout-control-panel hr,.layout-control-panel .button-view-map,.layout-control-panel .button-view-feeder{display:none}}@media only screen and (min-width: 1024px){.layout-control-panel{width:80px;height:calc(100vh - 80px);flex-direction:column;position:absolute;left:0;top:0}.layout-control-panel .select-view{display:none}.layout-control-panel hr,.layout-control-panel .button-view-map,.layout-control-panel .button-view-feeder{display:block}.layout-control-panel hr{width:100%;margin-top:0}.layout-control-panel button{margin-bottom:16px}.layout-control-panel .button-view-filters{margin-right:0}}
