.map-layer-toggle{position:absolute;right:20px;bottom:30px}.map-layer-toggle .map-layer-toggle-pane{background-color:#fff;border-top-right-radius:5px;border-top-left-radius:5px;overflow:hidden;box-shadow:1px 1px 2px #ccc}.map-layer-toggle .map-layer-toggle-pane .layer{padding:0 1.2rem 0 1rem}.map-layer-toggle .map-layer-toggle-pane .layer label{cursor:pointer}.map-layer-toggle .map-layer-toggle-pane .layer input{margin-right:0.35rem;cursor:pointer}.map-layer-toggle .map-layer-toggle-pane .layer:first-of-type{padding-top:1rem}.map-layer-toggle .map-layer-toggle-pane .layer:last-of-type{padding-bottom:0.4rem}.map-layer-toggle .map-layer-toggle-pane.panel-hide{max-height:0;transition:max-height 400ms ease-out}.map-layer-toggle .map-layer-toggle-pane.panel-show{max-height:300px;transition:max-height 400ms ease-in-out}.map-layer-toggle .map-layer-toggle-btn{width:100%}.map-layer-toggle .map-layer-toggle-btn .icon{margin-right:0.5rem}
