.map-panel-slideout{background-color:#fff;height:100%;position:absolute;top:0;left:-350px;transition:left 500ms ease-in-out;width:350px;z-index:1999;box-shadow:1px 1px 2px #ddd}.map-panel-slideout.active{left:80px;transition:left 500ms ease-in-out}.map-panel-slideout .map-panel-slideout-filters,.map-panel-slideout .map-panel-slideout-info{display:none}.map-panel-slideout.panel-view-filters .map-panel-slideout-filters{display:block}.map-panel-slideout.panel-view-info .map-panel-slideout-info{display:block}
